import {
  initFeature,
  initJourney,
  initLoadAbout,
  initLoadHome,
  initLoadPortfolio,
  initLoadServices,
  initNavbarScroll,
  initNavigation,
  initPopups,
  initProcess,
  initService,
  initValue,
} from './animations/cutting-edge';
import { initAos, initAosScale, initStaggeredAos } from './plugins/aos';
import { initTrFlip } from './plugins/flip-scrub';
import { initLenis } from './plugins/lenis';
import { getLenis } from './plugins/lenis';
import { initTrMarquee } from './plugins/scroll-marquee';
import {
  initFooterOverlap,
  initSectionOverlapFull,
  initSectionOverlapNext,
  initSectionOverlapPrev,
} from './plugins/section-overlap';
import { showHideGrid } from './plugins/showHideGrid';
import {
  createGradientHeadingTimeline,
  createSplitText,
  initFadeEatchWordOnScroll,
  splitLines,
} from './plugins/split-text';

CustomEase.create('primary-ease', '0.62, 0.05, 0.01, 0.99');
CustomEase.create('primary-ease-out', '0.17,0.84,0.44,1');
CustomEase.create('smooth', '.15, .9, .34, .95');
CustomEase.create('custom', '.76, 0, .24, 1');

ScrollTrigger.defaults({
  markers: false,
});

function initApp() {
  const mm = gsap.matchMedia();

  initLenis();
  window.lenis = getLenis();
  showHideGrid();
  //reusable functions
  splitLines();
  initFadeEatchWordOnScroll();
  createSplitText();
  initAos();
  initAosScale();
  initStaggeredAos();
  initTrMarquee();
  initTrFlip();

  // Cutting Edge Animations
  initFeature();
  initJourney();
  initService();
  initProcess();
  initNavbarScroll();
  initNavigation();
  initPopups();

  // load animation
  initLoadHome();
  initLoadAbout();
  initLoadServices();
  initLoadPortfolio();

  mm.add('(min-width: 992px)', () => {
    initSectionOverlapPrev();
    initSectionOverlapNext();
    initSectionOverlapFull();
    initFooterOverlap();
  });

  mm.add('(max-width: 768px)', () => {
    initValue();
  });
}

document.addEventListener('DOMContentLoaded', () => {
  initApp();
});
