import { createGradientHeadingTimeline, createSplitLines } from 'src/plugins/split-text';
import { Autoplay } from 'swiper/modules';

let scrollY = 0;

function lockScroll() {
  scrollY = window.scrollY || window.pageYOffset;

  document.documentElement.style.overflow = 'hidden';

  document.body.style.position = 'fixed';
  document.body.style.top = `-${scrollY}px`;
  document.body.style.left = '0';
  document.body.style.right = '0';
  document.body.style.width = '100%';
}

function unlockScroll() {
  document.documentElement.style.overflow = '';

  document.body.style.position = '';
  document.body.style.top = '';
  document.body.style.left = '';
  document.body.style.right = '';
  document.body.style.width = '';

  window.scrollTo(0, scrollY);
}

export function initFeature() {
  const wrapper = document.querySelector('.feature-track-wrap');
  if (!wrapper) return;

  const trackLines = wrapper.querySelector('.feature-track-line');

  gsap.set(trackLines, { scaleY: 0, transformOrigin: 'top center' });

  gsap.to(trackLines, {
    scaleY: 1,
    ease: 'none',
    scrollTrigger: {
      trigger: wrapper,
      start: 'top center',
      end: 'bottom center',
      scrub: true,
    },
  });
}

export function initJourney() {
  const wrapper = document.querySelector('.journey-wrapper');
  if (!wrapper) return;

  const items = wrapper.querySelectorAll('.journey-item');
  const trackWrap = wrapper.querySelector('.journey-track-wrap');
  const trackLine = wrapper.querySelector('.journey-track-line');

  gsap.set(trackLine, { scaleY: 0, transformOrigin: 'top center' });

  gsap.to(trackLine, {
    scaleY: 1,
    ease: 'none',
    scrollTrigger: {
      trigger: trackWrap,
      start: 'top center',
      end: 'bottom center',
      scrub: true,
    },
  });

  items.forEach((item, index) => {
    const dot = item.querySelector('.journey-dot');
    if (!dot) return;

    ScrollTrigger.create({
      trigger: dot,
      start: 'center center',
      end: 'center center',
      onEnter: () => dot.classList.add('is-active'),
      onEnterBack: () => dot.classList.add('is-active'),
      onLeaveBack: () => dot.classList.remove('is-active'),
    });
  });
}

export function initService() {
  const wrapper = document.querySelector('.service-wrapper');
  if (!wrapper) return;

  const content = wrapper.querySelector('.swiper');
  const pagination = wrapper.querySelector('.service-pagination-list');

  const slides = Array.from(content.querySelectorAll('.swiper-slide'));

  const menu = slides.map((slide) => slide.dataset.menu);

  //   const menu = [
  //     'Brand Development',
  //     'Marketing Strategy',
  //     'Retail Placement',
  //     'Content Production',
  //     'Media Buying',
  //     'Website Design',
  //     'E-Commerce',
  //     'Search Engine Optimization',
  //     'Paid Advertising',
  //     'Social Media',
  //   ];

  const swiper = new Swiper(content, {
    // Swiper options
    slidesPerView: 2,
    spaceBetween: 56,
    loop: false,
    centeredSlides: true,
    speed: 600,
    pagination: {
      el: pagination,
      clickable: true,
      bulletClass: 'service-pagination-item',
      bulletActiveClass: 'is-active',
      renderBullet: function (index, className) {
        return '<span class="' + className + '">' + menu[index] + '</span>';
      },
    },
    breakpoints: {
      0: {
        slidesPerView: 1,
        spaceBetween: 24,
      },
      768: {
        slidesPerView: 2,
        spaceBetween: 56,
      },
    },
  });
}

export function initProcess() {
  const content = document.querySelector('.process-content');
  if (!content) return;

  const progressLine = content.querySelectorAll('.process-pagination-progress');

  const swiperThumbs = new Swiper('.swiper.is-process-pagination', {
    slidesPerView: 4,
    spaceBetween: 32,
    speed: 600,
    watchSlidesProgress: true,
    breakpoints: {
      0: {
        slidesPerView: 1,
        spaceBetween: 16,
      },
      768: {
        slidesPerView: 4,
        spaceBetween: 32,
      },
    },
  });

  const swiperMain = new Swiper('.swiper.is-process-main', {
    slidesPerView: 1,
    spaceBetween: 32,
    speed: 600,
    autoplay: {
      delay: 4000,
      disableOnInteraction: false,
    },
    effect: 'fade',
    fadeEffect: {
      crossFade: true,
    },

    thumbs: {
      swiper: swiperThumbs,
    },

    on: {
      autoplayTimeLeft(swiper, time, progress) {
        const { activeIndex } = swiper;

        progressLine.forEach((el, i) => {
          if (i < activeIndex) {
            // slide yang sudah lewat
            el.style.transform = 'scaleX(1)';
          } else if (i === activeIndex) {
            // slide aktif (animating)
            el.style.transform = `scaleX(${1 - progress})`;
          } else {
            // slide setelahnya
            el.style.transform = 'scaleX(0)';
          }
        });
      },
    },
  });
}

export function initValue() {
  const content = document.querySelector('.value-content');
  if (!content) return;

  const swiper = new Swiper('.swiper.is-value', {
    slidesPerView: 1,
    spaceBetween: 24,
    centeredSlides: true,
    speed: 600,
  });
}

export function initNavbarScroll() {
  const navbar = document.querySelector('.navbar');
  if (!navbar || !window.lenis) return;

  const SCROLL_THRESHOLD = 20; // px

  lenis.on('scroll', ({ scroll }) => {
    if (scroll > SCROLL_THRESHOLD) {
      navbar.classList.add('is-scrolled');
    } else {
      navbar.classList.remove('is-scrolled');
    }
  });
}

export function initNavigation() {
  const nav = document.querySelector('.navbar');
  const toggle = document.querySelector('.nav-toggle');
  const navWrap = document.querySelector('.nav-mobile');
  if (!nav || !toggle || !navWrap) return;
  const link = navWrap.querySelectorAll('.nav-link');
  const btn = navWrap.querySelector('.btn');

  const mobileWrap = navWrap.querySelector('.nav-mobile-wrap');

  let isOpen = false;

  gsap.set(navWrap, {
    y: '0vh',
  });

  gsap.set(mobileWrap, {
    autoAlpha: 0,
  });

  //   gsap.set(link, {
  //     autoAlpha: 0,
  //     y: '2em',
  //   });

  //   gsap.set(btn, {
  //     autoAlpha: 0,
  //     scale: 0.3,
  //   });

  // Timeline untuk buka tutup menu

  const tl = gsap.timeline({ paused: true });

  // Animasi buka menu
  tl.to(
    navWrap,
    {
      y: '100vh',
      duration: 0.8,
      ease: 'primary-ease',
    },
    0
  );
  tl.to(
    mobileWrap,
    {
      autoAlpha: 1,
      duration: 0.4,
      ease: 'custom',
    },
    0.4
  );
  //   tl.to(
  //     link,
  //     {
  //       autoAlpha: 1,
  //       y: '0em',
  //       stagger: {
  //         each: 0.1,
  //         from: 'start',
  //       },
  //       duration: 0.6,
  //       ease: 'power2.out',
  //     },
  //     0
  //   );

  //   tl.to(
  //     btn,
  //     {
  //       autoAlpha: 1,
  //       scale: 1,
  //       duration: 0.8,
  //       ease: 'custom',
  //     },
  //     0
  //   );

  // Hamburger morph
  tl.to(
    '.line.top',
    {
      morphSVG: 'M3.9 15L15.5 5', // garis miring /
      duration: 0.4,
    },
    0
  )
    .to(
      '.line.bottom',
      {
        morphSVG: 'M4.3 4.6L15.2 15.5', // garis miring \
        duration: 0.4,
      },
      0
    )
    .to(
      '.line.middle',
      {
        opacity: 0,
        duration: 0.2,
      },
      0
    );

  toggle.addEventListener('click', () => {
    if (isOpen) {
      // Tutup menu → cukup reverse tanpa scramble
      tl.reverse();
      lenis.start();
      unlockScroll();
    } else {
      // Buka menu
      tl.play();
      lenis.stop();
      lockScroll();
    }
    isOpen = !isOpen;
  });

  link.forEach((el) => {
    el.addEventListener('click', () => {
      if (isOpen) {
        tl.reverse();
        isOpen = false;
      }
    });
  });

  btn.addEventListener('click', () => {
    if (isOpen) {
      tl.reverse();
      isOpen = false;
      lenis.start();
      unlockScroll();
    }
  });
}

export function initPopups() {
  const popupLinks = document.querySelectorAll('[data-popup-trigger]');
  const popup = document.querySelector('.contact-popup');
  const popupClose = popup?.querySelector('.close-btn-toggle');
  const popupInner = popup?.querySelector('.contact-wrapper');
  const popupOverlay = popup?.querySelector('.popup-overlay');

  if (!popup || !popupInner || !popupClose || !popupOverlay) return;
  let isOpen = false;
  gsap.set(popup, {
    display: 'none',
  });
  gsap.set(popupOverlay, {
    autoAlpha: 0,
  });
  gsap.set(popupInner, {
    autoAlpha: 0,
  });

  const tl = gsap.timeline({ paused: true });
  tl.to(popup, {
    display: 'flex',
    duration: 0,
    ease: 'power2.out',
  });
  tl.to(
    popupOverlay,
    {
      autoAlpha: 1,
      duration: 0.3,
      ease: 'none',
    },
    0
  );
  tl.to(
    popupInner,
    {
      autoAlpha: 1,
      duration: 0.3,
      ease: 'none',
    },
    0
  );

  popupLinks.forEach((link) => {
    link.addEventListener('click', (e) => {
      e.preventDefault();
      if (!isOpen) {
        tl.play();
        isOpen = true;
        lenis.stop();
        lockScroll();
      }
    });
  });

  function closePopup() {
    if (isOpen) {
      tl.reverse();
      isOpen = false;
      lenis.start();
      unlockScroll();
    }
  }
  popupClose.addEventListener('click', closePopup);
}

export function initLoadHome() {
  const wrapper = document.querySelector('.hero-wrapper.is-home');
  if (!wrapper) return;

  const title = wrapper.querySelector('.tittle-wrap');
  const heading = wrapper.querySelector('.gradient-heading');
  const text = wrapper.querySelector('.text-24');
  const partner = wrapper.querySelectorAll('.hero-partner');

  const splitText = new SplitText(text, {
    type: 'lines',
    linesClass: 'line',
    mask: 'lines',
  });

  const tl = gsap.timeline();

  gsap.set(title, { autoAlpha: 0, y: '2em' });
  gsap.set(partner, { autoAlpha: 0, y: '2em' });
  gsap.set(splitText.lines, { y: '100%' });

  tl.to(title, {
    autoAlpha: 1,
    y: '0em',
    duration: 1,
    ease: 'power2.out',
  });
  tl.add(createGradientHeadingTimeline(heading), '<0.2');
  tl.to(
    splitText.lines,
    {
      y: '0%',
      stagger: {
        each: 0.1,
        from: 'start',
      },
      duration: 0.8,
      ease: 'power2.out',
    },
    '<0.2'
  );

  tl.to(
    partner,
    {
      autoAlpha: 1,
      y: '0em',
      stagger: {
        each: 0.2,
        from: 'start',
      },
      duration: 0.8,
      ease: 'power2.out',
    },
    '<0.2'
  );
}

